import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDown } from "@fortawesome/free-solid-svg-icons";
import HeaderDetail from "../component/HeaderDetail";

const menuData = [
  [
    "polic 사용법",
    "polic 수수료 및 정산",
    "수수료 제로 프로그램",
    "polic pro 도전하기",
    "polic 판매취소 서비스",
    "polic 분쟁해결",
    "polic 확장 앱"
  ],
  [
    "비즈니스를 위한 polic",
    "클라우드 확장하기",
    "기획전 도전하기",
    "설정 값 변경하기",
    "polic 계정 연동",
    "polic 업체 수수료",
    "polic 안전거래"
  ],
  [
    "초상권 수집 방법",
    "클라우드 자동 구독",
    "통합을 위한 개발자센터",
    "polic 파트너를 통한 지원",
    "polic 판매취소 서비스",
    "polic 분쟁해결",
    "polic 확장 앱"
  ]
];

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      detailStatus: "detail__close",
      detailID: null,
      contents: [null]
    };
  }

  changeDetail(id) {
    let status = this.state.detailStatus;
    let currentId = this.state.detailID;

    if (status === "detail__close") {
      if (currentId !== id) {
        this.setState({ detailID: id });
        this.setState({ contents: menuData[id] });
      }
      console.log("ok");
      this.setState({ detailStatus: "detail__open" });
    } else if (currentId !== id) {
      this.setState({ detailID: id });
      this.setState({ contents: menuData[id] });
    } else {
      this.setState({ detailStatus: "detail__close" });
    }
  }

  render() {
    console.log(this.state);
    return (
      <header className="header">
        <div className="header__bar">
          <div className="header__box">
            <img
              className="header__logo"
              src={require("../images/logo_horizon.png")}
              alt="logo"
            />
            <ul className="header__menu">
              <li
                className="header__menu-items"
                onClick={() => {
                  this.changeDetail(0);
                }}
              >
                <span>개인</span>
                <FontAwesomeIcon icon={faAngleDown}></FontAwesomeIcon>
              </li>
              <li
                className="header__menu-items"
                onClick={() => {
                  this.changeDetail(1);
                }}
              >
                <span>비즈니스</span>
                <FontAwesomeIcon icon={faAngleDown}></FontAwesomeIcon>
              </li>
              <li
                className="header__menu-items"
                onClick={() => {
                  this.changeDetail(2);
                }}
              >
                <span>비즈니스 지원</span>
                <FontAwesomeIcon icon={faAngleDown}></FontAwesomeIcon>
              </li>
            </ul>
            <div className="header__btn-box">
              <button>로그인</button>
              <button>회원가입</button>
            </div>
          </div>
        </div>
        <HeaderDetail
          key={this.state.id}
          id={this.state.detailID}
          status={this.state.detailStatus}
          contents={this.state.contents}
        />
      </header>
    );
  }
}

export default Header;
