import React from "react";
import "./HeaderDetail.css";

class HeaderDetail extends React.Component {
  closeDetailMenu() {
    const detailBox = document.getElementById("detail__box");
    detailBox.classList.add("detail__close");
    detailBox.classList.remove("detail__open");
  }

  render() {
    console.log(this.props);
    return (
      <div id="detail__box" className={this.props.status}>
        <div id="menu__box" className="menu__box">
          {this.props.contents.map(text => {
            return <a href="#">{text}</a>;
          })}
        </div>
      </div>
    );
  }
}

export default HeaderDetail;
