import React from "react";
import "./Carusel.css";

const carData = [
  {
    id: 2,
    imgURL: require("../images/camera.jpg"),
    title: "비즈니스가 성장하고 있습니까? 이제 폴릭을 시작할 때 입니다.",
    desc:
      "걱정없이 촬영하세요. 언제든지 최고의 결과물을 가져다드리겠습니다. 믿고 맡기세요."
  },
  {
    id: 1,
    imgURL: require("../images/wallpaper.jpg"),
    title: "혁신을 경험하고 업그레이드 해주세요.",
    desc:
      "이젠 촬영으로 새로운 제품을 무료로 받아볼 수 있습니다. 지금 골라보세요!"
  }
];

let previousID = 1;

class Carusel extends React.Component {
  removePreviousSlideClass(close, show) {
    close.classList.remove("showing__left");
    show.classList.remove("closing__left");
    close.classList.remove("showing__right");
    show.classList.remove("closing__right");
  }

  slideToLeft(close, show) {
    this.removePreviousSlideClass(close, show);
    close.classList.add("closing__left");
    show.classList.add("showing__left");
  }

  slideToRight(close, show) {
    this.removePreviousSlideClass(close, show);
    close.classList.add("closing__right");
    show.classList.add("showing__right");
  }

  componentDidUpdate() {
    console.log("carusel updated");
    const closingBox = document.getElementById(previousID);
    const showingBox = document.getElementById(this.props.id);
    if (this.props.direction === "left") {
      this.slideToLeft(closingBox, showingBox);
    } else {
      this.slideToRight(closingBox, showingBox);
    }
    previousID = this.props.id;
  }

  componentDidMount() {
    previousID = this.props.id;
  }

  render() {
    return (
      <div className="carusel">
        {carData.map(data => {
          return (
            <div
              id={data.id}
              className="carusel__box"
              style={{
                backgroundImage: `url(${data.imgURL})`
              }}
            >
              <div className="carusel__contents">
                <h1>{data.title}</h1>
                <p>{data.desc}</p>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default Carusel;
