import React from "react";
import Carusel from "../component/Carusel";
import "./Slider.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleLeft, faAlignLeft } from "@fortawesome/free-solid-svg-icons";
import { faAngleRight } from "@fortawesome/free-solid-svg-icons";

class Slider extends React.Component {
  state = {
    id: 1,
    direction: "left"
  };

  leftSlide() {
    console.log("button left Clicked");
    let currentID = this.state.id;
    this.setState({ direction: "left" });
    if (currentID <= 1) {
      this.setState({ id: 2 });
    } else {
      this.setState({ id: currentID - 1 });
    }
  }

  rightSlide() {
    console.log("button right Clicked");
    let currentID = this.state.id;
    this.setState({ direction: "right" });
    if (currentID >= 2) {
      this.setState({ id: 1 });
    } else {
      this.setState({ id: currentID + 1 });
    }
  }

  render() {
    return (
      <div className="slider">
        <FontAwesomeIcon
          icon={faAngleLeft}
          className="slider__button left"
          style={{ width: "40px", height: "40px" }}
          onClick={() => {
            this.leftSlide();
          }}
        />
        <Carusel id={this.state.id} direction={this.state.direction} />
        <FontAwesomeIcon
          icon={faAngleRight}
          className="slider__button right"
          style={{ width: "40px", height: "40px" }}
          onClick={() => {
            this.rightSlide();
          }}
        />
      </div>
    );
  }
}

export default Slider;
